.warpBox {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.warpBox .header_box {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
}

.warpBox .w_box {
  margin-top: 1rem;
  margin-bottom: 1.2rem;
  overflow-y: scroll;
  overflow-x: hidden;
}

.warpBox .w_box .banner {
  text-align: center;
}

.warpBox .w_box .goodsinfo {
  width: 100%;
  height: 3rem;
  padding: .25rem;
  box-sizing: border-box;
}

.warpBox .w_box .goodsinfo .g_titi {
  font-size: .45rem;
  line-height: .8rem;
}

.warpBox .w_box .goodsinfo .g_info {
  font-size: .42rem;
  line-height: .8rem;
  color: #666;
}

.warpBox .w_box .goodsinfo .g_price {
  font-size: .4rem;
}

.warpBox .w_box .goodsinfo .g_price .pay {
  color: red;
}

.warpBox .w_box .goodsinfo .g_price .origin_pay {
  color: #666;
  text-decoration: line-through;
}

.warpBox .w_box .goodsinfo .productifo {
  display: flex;
  justify-content: space-between;
  padding: 0 .2rem;
  background: #fcf;
  color: #c69;
}

.warpBox .w_box .goodsinfo .productifo p {
  line-height: .6rem;
  font-size: .36rem;
}

.warpBox .w_box .goodsinfo .productifo p span {
  text-align: center;
}

.warpBox .w_box .line {
  height: .2rem;
  background: #f2f2f2;
}

.warpBox .w_box .select_box {
  height: 4.2rem;
  padding: .4rem .2rem;
  box-sizing: border-box;
}

.warpBox .w_box .select_box .s_item {
  display: flex;
  justify-content: space-between;
  padding: .3rem .2rem;
}

.warpBox .w_box .select_box .s_item span {
  font-size: .38rem;
  color: #666;
}

.warpBox .w_box .produce_msg .p_tit {
  line-height: 1.3rem;
  display: flex;
  justify-content: space-between;
  padding: 0 .2rem;
}

.warpBox .w_box .produce_msg .p_tit .good {
  font-size: .45rem;
}

.warpBox .w_box .produce_msg .p_tit .good_msg {
  font-size: .28rem;
  color: red;
}

.warpBox .w_box .produce_msg .p_tit .good_msg i {
  font-size: .6rem;
  color: #000;
}

.warpBox .w_box .produce_msg .g_msgs {
  padding: .26rem;
}

.warpBox .w_box .produce_msg .g_msgs .user_info {
  display: flex;
  justify-content: space-between;
}

.warpBox .w_box .produce_msg .g_msgs .user_info .userIcon {
  display: flex;
}

.warpBox .w_box .produce_msg .g_msgs .user_info .userIcon img {
  align-self: center;
}

.warpBox .w_box .produce_msg .g_msgs .user_info .userIcon i {
  margin-left: .1rem;
  font-size: .28rem;
  font-style: normal;
  align-self: center;
}

.warpBox .w_box .produce_msg .g_msgs .user_info .start img {
  margin: 0;
  padding: 0;
}

.warpBox .w_box .produce_msg .g_msgs .msgs {
  padding: .2rem;
  margin-top: .3rem;
  font-size: .28rem;
  line-height: .6rem;
  color: #666;
}

.warpBox .footer_bar {
  width: 100%;
  height: 1.2rem;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  display: flex;
}

.warpBox .footer_bar .icon_box {
  flex: 1 1;
  display: flex;
}

.warpBox .footer_bar .icon_box .icon_box_ {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.warpBox .footer_bar .icon_box .icon_box_ img {
  width: 70%;
  align-self: center;
}

.warpBox .footer_bar .icon_box .icon_box_ span {
  text-align: center;
}

.warpBox .footer_bar ._but_box {
  flex: 2 1;
  display: flex;
  justify-content: space-around;
  text-align: center;
}

.warpBox .footer_bar ._but_box b:nth-child(1) {
  flex: 1 1;
  background-color: #666;
  color: #fff;
  line-height: 1.2rem;
}

.warpBox .footer_bar ._but_box b:nth-child(1) img {
  margin-right: .2rem;
}

.warpBox .footer_bar ._but_box b:nth-child(2) {
  flex: 1 1;
  background-color: #ff4794;
  color: #fff;
  line-height: 1.2rem;
}